export default function Projects() {
    const classes = useStyles();
    const [selected, setSelected] = React.useState(-1);
    return (
        <div>
            <Typography component="div">
                <div style={{ display: "flex", alignItems: "center", marginLeft: 10 }}>
                    <Settings color="primary" style={{ fontSize: 40 }} />
                    <Box fontWeight="bold" fontSize={40} m={2}>
                        projects
                    </Box>
                </div>
            </Typography>
            {selected === -1 ?(
                <List className={classes.root}>
                {
                    proj.map((item,index)=>
                        <ListItem alignItems="flex-start" key={item.title}>
                            <ListItemAvatar>
                                <img style={{margin:10}} height="100" width="100" src={item.img} alt={item.title}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary={
                                    <Typography
                                            component="span"
                                            variant="h6"
                                            className={classes.inline}
                                            color="primary"
                                        >
                                            {item.title}
                                    </Typography>
                                }
                                secondary={
                                    <Typography component="div">
                                        <Box fontSize={14} m={1} textAlign="justify" style={{color:"white"}}>
                                        {item.text}
                                        </Box>
                                        <Button variant="contained" color="primary" style={{padding:"2px 10px",float:"right"}} onClick={()=>setSelected(index)}>
                                            More
                                        </Button>
                                    </Typography>
                                }
                            />
                        </ListItem>
                    )
                }
                </List>
            ):(
                <div style={{display:"flex",flexDirection:"column",padding:10}}>
                    <div>
                        <Button variant="contained" color="primary" style={{padding:"2px 10px"}} onClick={()=>setSelected(-1)}>
                            Back
                        </Button>
                    </div>
                    <hr style={{background:"#a8a8a8",width:"100%",margin:'20px 0px'}} />
                    <div>
                        <Typography
                            component="span"
                            variant="h5"
                            className={classes.inline}
                            color="primary"
                        >
                            {proj[selected].title}
                        </Typography>

                        {render(selected)}
                    </div>
                </div>
            )}
            
        </div>
    )
}